import React, { useEffect, useState } from "react";
import StoreList from "../containers/StoreList";
import { findStore } from "../library/parse";

const Home = () => {
  const [stores, setStores] = useState([]);
  useEffect(() => {
    console.log('finding store')
    findStore().then((e) => setStores(e.filter(s => s.get('display'))));
  }, []);
  return <>{!!stores.length && <StoreList publicPage stores={stores} />}</>;
};

export default Home;
